*{
    cursor: none;
}

.large_circle,
.small_circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: transparent;
  border: 2px solid grey;
  position: fixed;
  z-index: 1000;
  pointer-events: none;
  transition: transform 0.1s;
}
.small_circle {
  width: 10px;
  height: 10px;
  background-color: grey;
}